.Holiday_primary {
  background-color: #ffffff;
  border-radius: 50%;
  height: 6px;
  width: 6px;
  border: 2px solid #df5146;
  padding: 0px;
  position: relative;
  top: -29px;
  left: 21px;
}
.wh_container .item2 {
  width: 26px;
  height: 26px;
  border: 1px solid #2f6aff;
  border-radius: 50%;
}
.half-day-holiday {
  display: inline-block;
  position: relative;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  border: solid 2px $color-main;
  margin-right: 5px;
  overflow: hidden;
  .half-day-circle {
    display: inline-block;
    position: absolute;
    left: -1px;
    top: 0px;
    width: 3.5px;
    height: 6px;
    background: $color-main;
  }
}
.circle-zero {
  display: inline-block;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  border: solid 2px #df5146;
  margin-right: 5px;
}
.circle-one {
  display: inline-block;
  position: relative;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  border: solid 2px $color-main;
  margin-right: 5px;
  overflow: hidden;
  .circle-one-child {
    display: inline-block;
    position: absolute;
    left: 0px;
    top: 0px;
    width: 3px;
    height: 5px;
    border-radius: 50%;
    background: $color-main;
  }
}
.circle-two {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: $color-main;
  margin-right: 5px;
}
